.upwards,
.downwards {
  display: flex;
  justify-content: center;
}

.sideways {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.left,
.right {
  margin: 50px;
  padding: 20px;
  /* border: 5px dashed; */
  border-radius: 500px;
  border: 2px dashed white;
  color: #dc042c;
  font-style: Fantasy;
  /* background: #1e1e1e; */

  background: #ecec4e;
  /* box-shadow: 5px -5px 10px rgba(0, 0, 0, 0.6); */
}

.up,
.down {
  padding: 20px;
  /* border: 5px dashed; */
  border-radius: 20px;
  border: 5px dashed white;
  color: #dc042c;
  background: #ecec4e;
  /* box-shadow: 5px -5px 10px rgba(0, 0, 0, 0.6); */
}
