.wrapper {
  position: relative;
  width: 250px;
  height: 250px;
  border: 2px solid #0c0b0b;
  /* border-radius: 10px; */
  margin: 50px auto;
  margin-top: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* box-shadow: 0 0 10px #abbfc0; */
}

.start {
  margin: 100px;
  background: yellowgreen;
  color: brown;
  border-radius: 7px;
  border: 2px inset black;
  padding: 10px;
  font-size: 1.5em;
  box-shadow: 0 0 70px #abbfc0;
  font-family: "Courier New", Courier, monospace;
}
